import React, { useState, useEffect, useMemo } from "react";
import { onAuthStateChanged, User, signInAnonymously } from "firebase/auth"; // Import signInAnonymously
import { auth } from "@/firebase/config";
import * as firebaseui from "firebaseui";
import FirebaseUIWidget from "./ui/FirebaseUIWidget";

const MyComponent: React.FC = () => {
  const [user, setUser] = useState<User | null>(null);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (firebaseUser) => {
      setUser(firebaseUser || null);
    });
    return () => unsubscribe(); // Cleanup subscription
  }, []);

  // Use useMemo to memoize uiConfig
  const uiConfig = useMemo<firebaseui.auth.Config>(
    () => ({
      signInFlow: "popup",
      signInSuccessUrl: "/signedIn", // Or a callback function
      signInOptions: [firebaseui.auth.AnonymousAuthProvider.PROVIDER_ID],
      callbacks: {
        signInSuccessWithAuthResult: () => false, // Prevent redirect
      },
    }),
    [],
  ); // Empty dependency array means it only runs once

  const handleAnonymousSignIn = async () => {
    try {
      await signInAnonymously(auth); // Call signInAnonymously with the auth instance
    } catch (error) {
      console.error("Anonymous sign-in error:", error);
    }
  };

  return (
    <div>
      {user ? (
        <p>Welcome, {user.email ?? "Guest"}!</p>
      ) : typeof window !== "undefined" ? ( // Conditional rendering
        <>
          <button onClick={handleAnonymousSignIn}>Sign In Anonymously</button>
          <FirebaseUIWidget uiConfig={uiConfig} />
        </>
      ) : (
        <p>Loading...</p> // Or a placeholder message
      )}
    </div>
  );
};

export default MyComponent;
